.mainCardCont {
  border-radius: 20px;
  padding: 5px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 10px #353535;
}
.mainCardCont img {
  filter: brightness(0.7);
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  pointer-events: none;
  z-index: -1;
  animation: openAnim 0.3s forwards;
}
@keyframes openAnim {
  from {
    transform: scale(1.4);
  }
  to {
    transform: scale(1);
  }
}
.mainCardCont p {
  margin: 10px 0 0 10px;
  color: #e2fffb;
  font-size: 30px;
  font-family: Azeret;
  font-weight: bold;
  text-shadow: 0 0 5px rgba(33, 74, 222, 0.4588235294);
  z-index: 10 !important;
}
.mainCardCont a {
  z-index: 10;
  position: absolute;
  bottom: 20px;
  right: 20px;
  transform: rotate(-40deg);
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  font-size: 30px;
  padding: 5px;
  color: #214ade;
  background-color: #fff;
  box-shadow: 0 0 0 5px rgba(33, 74, 222, 0.4588235294);
  transition: 0.4s;
  cursor: pointer;
}
.mainCardCont:hover a {
  transform: rotate(0);
}

@media (max-width: 700px) {
  .mainCardCont p {
    font-size: 23px !important;
  }
}
@media (max-width: 500px) {
  .mainCardCont {
    height: 150px !important;
  }
  .mainCardCont p {
    font-size: 16px !important;
  }
}
@media (max-width: 400px) {
  .mainCardCont p {
    font-size: 13px !important;
  }
}/*# sourceMappingURL=Maincard.module.css.map */